import React, {useState, useEffect} from 'react';
import Field from './Field'
import Timer from './Timer'
import Form from './Form'
import Results from './Results'
import './App.css';

function App() {
  const [roundsCount, setRoundsCount] = useState('')
  const [start, setStart] = useState(false)
  const [reset, setReset] = useState(false)
  const [showHistory, setShowHistory] = useState(false)

  const onStartPress = () => setStart(true)

  const onReset = () => setReset(true)

  const onChange = ({target: {value}}) => {
    if(!isNaN(value)) {
      setRoundsCount(value.trim())
    }
  }

  const onShowHistory = () => {
    setShowHistory(true)
  }

  const onHideHistory = () => {
    setShowHistory(false)
  }

  useEffect(() => {
    reset && setReset(false)
  }, [reset])

  if(reset) return (
    <div className="App">
      <div className={'container'}>
      </div>
    </div>
  )

  if(showHistory) {
    return (
      <div className={'App'}>
        <div className={'container form'}>
          <button className={'submit_btn start_btn'} onClick={onHideHistory}>–ù–∞–∑–∞–¥</button>
          <Results></Results>
        </div>
      </div>
    )
  }

  return (
    <div className="App">
      {start ? (
        <div className={'container'}>
          <Form count={Number(roundsCount) || 8} min={1} max={19} />
          <button className={'submit_btn reset_btn'} onClick={onReset}>–ó–∞–Ω–æ–≤–æ</button>
        </div>
      ) : (
        <div className={'greeting'}>
          <h2 className={'alina'}>–ü—Ä–∏–≤–µ—Ç, –ê–ª–∏–Ω–∞!</h2>
          <div className={'fieldItem'}>
            <div className={'label'}>–°–∫–æ–ª—å–∫–æ –ø—Ä–∏–º–µ—Ä–æ–≤ –±—É–¥–µ–º —Ä–µ—à–∞—Ç—å?</div>
            <input inputMode={'numeric'} placeholder={'–ö–∞–∫ –Ω–∞—Å—á–µ—Ç 8?'} className={'roundInput'} onChange={onChange} value={roundsCount}/>
          </div>
          <button className={'submit_btn start_btn'} onClick={onStartPress}>–ù–∞—á–∞—Ç—å</button>
          <button className={'submit_btn start_btn'} onClick={onShowHistory}>–ú–æ–∏ –∑–∞–Ω—è—Ç–∏—è</button>
        </div>
      )}
    </div>
  );
}

export default App;
