import React from 'react'
import {getPeriodData} from '../helpers'
import './index.css'

function getTime(time) {
  const mins = Math.floor(time / 60000)
  const sec = time - (mins * 60000)

  return mins + ' –º–∏–Ω. ' + Math.floor(sec / 1000) + ' —Å–µ–∫.'
}

export default () => {
  const data = getPeriodData(50) || []

  if(!data.length) {
    return (
      <div>–ü–æ–∫–∞ –Ω–∏—á–µ–≥–æ –Ω–µ—Ç—É, —Å–∞–º–æ–µ –≤—Ä–µ–º—è —Ä–µ—à–∏—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ –∑–∞–¥–∞—á–µ–∫!</div>
    )
  }
  return (
    <div className={'historyList'}>
      {data.map((item) => {
        const date = new Date(item.time)
        const month = date.getMonth()
        const day = date.getDate()
        const totalCount = item.wrongCount + item.rightCount

        if(!item.time) return null

        return (
          <div key={item.time} className={'historyItem'}>
            <div className={'historyDate'}>{item.date}</div>
            <div className={'grayText'}>–í—Ä–µ–º—è –∑–∞–Ω—è—Ç–∏–π: <span className={'darkText'}>{getTime(item.time)}</span></div>
            <div className={'grayText'}>–ü—Ä–∞–≤–∏–ª—å–Ω—ã–µ –æ—Ç–≤–µ—Ç—ã: <span className={'rightText'}>{item.rightCount}</span></div>
            <div className={'grayText'}>–ù–µ–ø—Ä–∞–≤–∏–ª—å–Ω—ã–µ –æ—Ç–≤–µ—Ç—ã: <span className={'wrongText'}>{item.wrongCount}</span></div>
            <div className={'progressLine'}>
              <div className={'progressRight'} style={{flex: item.rightCount / totalCount}}></div>
              <div className={'progressWrong'} style={{flex: item.wrongCount / totalCount}}></div>
            </div>
          </div>
        )
      })}
    </div>
  )
}
